<template>
    <div class="search">
        <div class="top" >
            <van-icon name="arrow-left" class="lastpage" size="4rem" @click="toLast"/>
            <h2></h2>
        </div>

        <div class="search_div">
            <div class="search_input">
                <input type="text" placeholder="" v-model="keyword"> 
                <img @click="search" src="data:image/svg+xml;base64," alt=""> 
                <!----> 
                <img src="data:image/svg+xml;base64," alt="" class="close" style="display: none;">
            </div>
        </div>
   
        <div class="search_content" v-if="resultList.length != 0">
            <ul>
                <span style="font-size: 2.4rem;"> </span>
                <div class="result_item" v-for="item in resultList">
                    <img :src="item.video_cover" alt="" class="speak_img">
                    <div class="detail">
                        <span class="detail_title">{{item.title}}</span>
                        <span class="speaker">{{item.speaker.name}}&nbsp;&nbsp;{{item.speaker.intro}}</span> 
                        <span class="play_count">{{item.created}}</span>
                    </div> 
                    <div class="play">
                        <img src="data:image/svg+xml;base64," alt="">
                    </div>
                </div>
            </ul>
        </div>

        <div v-else></div>
    </div>
</template>
<script>

import { get } from 'utils/http.js'
export default{
    data(){
        return {
            isShow : false,
            keyword : '',
            datalist : [],
            resultList : []
        }
    },
    
    watch: {
        keyword(val){
            if(val === ''){
                this.resultList = []
            }
        } ,
        resultList(val){
            console.log(val)
        } 
    },
    async created () {
        let result = await get({
            url : '/datalist/detail'
        })
        this.datalist = result.data.items_speech
        // console.log(this.datalist)
    },

    methods: {
        search(){
            if(this.keyword){
                this.datalist.forEach((item)=>{
                    if(item.title.indexOf(this.keyword) != -1){
                        this.resultList.push(item)
                    }
                })
            }    
        },
        toLast(){
            this.$router.history.go(-1)
        }
    },
    
}
</script>
<style lang="stylus" scoped>
   
    .top 
        position relative
        height 9rem
        width 100%
        display flex
        box-shadow 0 0 1rem rgba(0,0,0,.1)
        .lastpage
            flex 1
            height 100%
            line-height 9rem
            text-align center
            
        h2 
            flex 9
            line-height 9rem
            text-align center
            font-size 3.6rem
    .search_div
        width 100%
        height 14rem
        background #fff
        -webkit-box-pack: center
        -ms-flex-pack center
        justify-content center
        margin-bottom 2px
        display flex
        -webkit-box-align center
        align-items center
        /* line-height 14rem */
        .search_input
            border .3px solid rgba(194,201,215,.4)
            width 67rem
            box-shadow 0 0 1rem rgba(0,0,0,.1)
            height 8rem
            padding 0 3rem
            -webkit-box-pack justify
            justify-content space-between
            border-radius 4.6rem
            line-height 8rem
            input
                width 54rem
                border none
                height 5.9rem
                font-size 2.4rem
                font-family PingFangSC-Regular
                font-weight 400
                color #000
            img
                
                width 4rem
                height 4rem;
        
    .result_item
        width 100%
        height 16rem
        display flex 
        justify-content flex-start
        position relative
        margin-bottom 3rem
        padding 0 4rem
        -webkit-box-pack start
        .speak_img
            width 28rem 
            height 16rem
            margin-right 4rem
            box-shadow 0 0 1rem rgba(0,0,0,.1)
        .detail
            width 31.4rem
            position relative
            box-sizing border-box
            .detail_title
                width 100%
                overflow hidden
                text-overflow: ellipsis
                font-size 2.8rem
                font-family PingFangSC-Medium
                font-weight 500
                display -webkit-box
                color #000
                -webkit-line-clamp 2
                -webkit-box-orient vertical
            .speaker
                width 100%
                font-size 2rem
                font-family PingFangSC-Regular
                font-weight 400
                color #6f6f6f
                overflow hidden
                text-overflow ellipsis
                display -webkit-box
                -webkit-line-clamp 2
                -webkit-box-orient vertical
            .play_count
                position absolute
                left 0
                bottom 0
                font-size 2rem
                font-family PingFangSC-Regular
                font-weight 400
                line-height 2rem
                color #6f6f6f
                width 100%
                display flex
                -webkit-box-pack justify
                justify-content space-between
        .play
            width 3.2rem
            height 16rem
            text-align center
            display flex
            -webkit-box-align center
            align-items center
            -webkit-box-pack center
            justify-content center
            img
                width 3.2rem
                height 3.2rem
    
</style>