<template>
    <div class="detail">
        <header>
            <img @click="toSpot" class="go_back" src="data:image/svg+xml;base64," alt="" >
            <span></span> 
        </header>
        <main>
            <speek_item_header :activity="activity"></speek_item_header>
            <!--  -->
            <div class="introduce">
                <div class="title"></div>
                <countyard_item
                v-for="detail in item_data"
                :key="detail.speech_id"
                :detail="detail"
               
                ></countyard_item>
            </div>    
        </main>
        <footer></footer>
    </div>
</template>
<script>
import countyard_item from './countyard_item'
import speek_item_header from './speek_details_header'

import { get } from 'utils/http.js'
export default{
    data(){
        return{
            id : 0,
            activity : {},
            item_data : [],
        }
    },

    components: {
        countyard_item,
        speek_item_header
    },
    methods: {
      toSpot(){
          this.$router.push('/spot')
      },
    },
    
    async created () {
        this.id = this.$route.params.id

        let data = await get({
            url : `/api/h5/activity/${this.id}`
        })
        this.activity = data.data.activity
        this.item_data = data.data.item_one
        // console.log(data)
        // console.log(this.activity)
        // console.log(this.item_data)

    }
}
</script>

<style lang="stylus" scoped>

header
    width 100%
    height 9rem
    background-color #fff
    overflow hidden
    font-size 3.6rem
    color #000
    font-family "SourceHanSerifSC"
    line-height 9rem
    text-align center
    position absolute
    top 0
    z-index 10
    left 0
    margin-bottom .2rem
    box-shadow 0 0 1rem rgba(0,0,0,.1)
    img
        width 3.2rem
        height 3.2rem
        position absolute
        top calc(50% - 1.6rem)
        left 3.5rem
main
    width 100%
    position absolute
    top 9rem
    bottom 9rem
    overflow scroll
    
    .introduce
        width 100%
        height auto
        background #fff
        padding 3.6rem 0 1rem 6rem  
        .title 
            font-size 2.8rem
            margin-bottom: 4rem;
        


footer
    position fixed
    bottom 0
    width 100%
    font-size 2.8rem
    line-height 10rem
    color #6f6f6f
    font-family PingFangSC-Regular
    font-weight 400
    text-align center
    box-shadow 0 0 1rem rgba(0,0,0,.1)
        
</style>