<template>
    <van-skeleton title avatar :row="10" :loading="loading">
    <div class="speech-detail"> 
        <div class="myviedo" v-if="recommend_speeches[0]">
            <img @click="autoplay" src="data:image/svg+xml;base64," alt="" class="play">
            <img :src="recommend_speeches[0].video_cover" alt="" class="video-play">
            <div class="video" v-if="recommend_speeches[0].video">
                <video preload="auto" x5-playsinline="" playsinline="true" webkit-playsinline="true" controls="controls" x5-video-player-fullscreen="true" x-webkit-airplay="allow" :src="recommend_speeches[0].video[1].video_url" style="object-fit: contain;" class="myvideo"></video>
            </div>
        </div>
        <div class="content">
            <div class="speecher" v-if="recommend_speeches[0]">
                <div class="title">{{recommend_speeches[0].episode_title}}</div>
                <img :src="recommend_speeches[0].speaker.avatar" alt="">
                <div class="speech-title">
                    <div>
                        <span>{{recommend_speeches[0].speaker.name}} - </span><span> {{recommend_speeches[0].speaker.intro}}</span>
                    </div>
                </div>
                <div class="speech-dev">
                    {{recommend_speeches[0].titlelanguage}}
                </div>
                <div class="timeshare">
                    <span>{{recommend_speeches[0].created}}</span><span>{{recommend_speeches[0].play_count}}</span>
                </div>
            </div>
            <div class="speech-check">
                <div class="check-left">
                    <img src="data:image/svg+xml;base64," alt="">
                    <span></span>
                </div>
                <div class="right">
                    <img src="data:image/svg+xml;base64," alt="">
                    <span></span>
                </div>
            </div>
            <div class="diversity">
                <div class="fen-title">
                    
                </div>
                <div>
                    <div v-for="item in recommend_speeches"  :key="item.id">
                        <span>{{item.episode}}</span>
                        <img :src="item.video_cover" alt="">
                    </div>
                </div>
            </div>
            <div class="discuss">
                <div class="discuss-tittle">
                    <span></span>
                </div>
                <div class="discuss-content">
                    <div class="discuss-item" v-for="cm in items" :key="cm.id">
                        <div class="user">
                            <div v-if="cm.user">
                                <img :src="cm.user.avater" alt="" class="user-img">
                                <span class="user-name">{{cm.user.nickname}}</span>
                                <span class="user-time">{{cm.created}}</span>
                            </div>
                            <div class="good">
                                <span>{{cm.like_count}}</span>
                                <img src="data:image/svg+xml;base64," alt="">
                            </div>
                        </div>
                        <div class="content-review">
                            {{cm.content}}
                        </div>
                        <div class="children-reviw" v-if="cm.reply_comments" v-show="cm.reply_comments.length>0 ? true : false">
                            <div class="children-reviw-item"  v-for="cn in cm.reply_comments" :key="cn.id">
                                <span class="children-name">{{cn.nickname}}</span>
                                <span>:{{cn.content}}</span>
                            </div>
                            <div class="add-discuss">{{cm.reply_comments.length}}</div>
                        </div>
                    </div>
                </div>
                <div class="lookall">
                    <div>
                        <span>{{comment_count}}</span>
                    </div>
                </div>
            </div>
            <div class="recommend">
                <img class="icon1" src="data:image/svg+xml;base64," alt="">
                <span class="tuijian"></span>
                <div class="recommend-item" v-for="it in recommend_zhiya_lists" :key="it.id">
