<script>
import Vue from 'vue';
import { Skeleton } from 'vant';
import axios from 'axios';
Vue.use(Skeleton);
import {mapState} from 'vuex'
export default {
        data() {
            return {
                active: 0,
                recommend_zhiya_lists: [],
                recommend_speeches: [],
                comment_count: 0,
                items: [],
                loading: true,
                id:0,
                name:''
            }
        },
        methods:{
            autoplay(){
                document.querySelector('.video').style.display = 'block';
            }
        },
        computed:{
            ...mapState(['allid','allname'])
        },
        mounted() {
            this.id= this.allid;
            this.name= this.allname;
            console.log(this.id);
            axios({
            url: `/api/h5/${this.name}/${this.id}`
            })
            .then((result) => {
                this.recommend_zhiya_lists=result.data.data.recommend_zhiya_lists;
                this.recommend_speeches=result.data.data.items;
                console.log('recommend_zhiya_lists:',this.recommend_zhiya_lists)
                console.log('recommend_speeches',this.recommend_speeches)
            });
                axios({
                url: `/api/h5/comments/?page=1&page_size=3&zhiyavideo_id=${this.id}`
                })
                .then((result) => {
                    this.comment_count = result.data.data.total;
                    this.items =result.data.data.items;
                    console.log('items',this.items);
                    this.loading = false;
                });
            
            
        },
    }
</script>

